import Admin from "../componentes/Admin";
import RodapePrincipal from "../componentes/RodapePrincipal"
import AccountMenu from "../componentes/AccountMenu";


function Cadastro() {
  return (

    <div id="wrapper">

      <Admin />

      <div id="content-wrapper" className="d-flex flex-column">

        <div id="content">

          <nav className="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">

            <ul className="navbar-nav ml-auto">
              <div className="topbar-divider d-none d-sm-block"></div>
              <AccountMenu />
            </ul>
          </nav>
          <div className="row justify-content-center">

            <div className="container-fluid">

              <div className="d-sm-flex justify-content-center mb-4 d-flex ">
                <h1 className="h3 mb-0 text-gray-800">Cadastro Membro</h1>
              </div>
              <div className="d-sm-flex justify-content-center mb-4 d-flex ">
                <a className="h3 mb-0 text-success "/>
              </div>
            </div>

          </div>
        </div>
        <RodapePrincipal />
      </div>
    </div>




  );
}

export default Cadastro;