import React, { useEffect, useState } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from 'react-router-dom';
import MenuPrincipal from './pages/MenuPrincipal';
import Cadastro from './pages/Cadastro';
import Chamada from './pages/Chamada';
import SignInSide from './pages/login';
import ConsultaVisitante from './pages/ConsultaVisitante';
import CriarEscala from './pages/CriarEscala';

const PrivateRoute = ({ element, isAuthenticated }) => {
 
 if (isAuthenticated === null) {
    // Se a autenticao ainda estiver sendo verificada, voc pode mostrar um indicador de carregamento.
    return <div>Verificando autenticao...</div>;
  }


  return isAuthenticated ? element : <Navigate to="/login/sso" />;

};

const Props = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(null);

  useEffect(() => {
    // Check for the presence of the session token or any other authentication logic.
    const token = localStorage.getItem('token'); // Change as needed.

    if (token) {
      console.log("entrei")
      setIsAuthenticated(true);
    } else {
      console.log("sai")
      setIsAuthenticated(false);
    }
  }, []);

  return (
    <Router>
      <Routes>
        <Route path="/Start-build" element={<SignInSide />} />
        <Route
          path="/"
          element={
            <PrivateRoute
              isAuthenticated={isAuthenticated}
              element={<MenuPrincipal />}
            />
          }
        />
        <Route
          path="/DashBoards"
          element={
            <PrivateRoute
              isAuthenticated={isAuthenticated}
              element={<MenuPrincipal />}
            />
          }
        />
        <Route
          path="/Cadastro"
          element={
            <PrivateRoute
              isAuthenticated={isAuthenticated}
              element={<Cadastro />}
            />
          }
        />
        <Route
          path="/Chamada"
          element={
            <PrivateRoute
              isAuthenticated={isAuthenticated}
              element={<Chamada />}
            />
          }
        />
        <Route
          path="/ConsultaVisitante"
          element={
            <PrivateRoute
              isAuthenticated={isAuthenticated}
              element={<ConsultaVisitante />}
            />
          }
        />
        <Route
          path="/CriarEscala"
          element={
            <PrivateRoute
              isAuthenticated={isAuthenticated}
              element={<CriarEscala />}
            />
          }
        />
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    </Router>
  );
};

export default Props;
